import React from 'react';

export const TaskForm = ({ handleAddTask }) => (
    <form id="add-task" onSubmit={handleAddTask}>
        <input type="text" placeholder="New Task..." className="new-task-input task-input" />
        <button className="button new-task-submit" >
            <span><i className="fas fa-plus icon"></i></span>
            <span>Add</span>
        </button>
    </form>
)
