import React from 'react';
import {NavLink} from 'react-router-dom';
// Configuring Font Awesome dependencies
// import {render} from 'react-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// Importing Icons 
import { faCloudSun } from '@fortawesome/free-solid-svg-icons'
import { faEdit } from '@fortawesome/free-solid-svg-icons'
import { faCalendarTimes } from '@fortawesome/free-solid-svg-icons'
import { faUserCircle } from '@fortawesome/free-solid-svg-icons'



const Navigation = () => (
    <nav id="main-menu">        
        <NavLink to="/" activeClassName="is-current" exact={true} className="nav-item">
            <FontAwesomeIcon icon={faCloudSun} />
        </NavLink>
        <NavLink to="/tasks" activeClassName="is-current" className="nav-item">
            <FontAwesomeIcon icon={faCalendarTimes} />
            </NavLink>
        <NavLink to="/notes" activeClassName="is-current" className="nav-item">
            <FontAwesomeIcon icon={faEdit} />
        </NavLink>
        <NavLink to="/settings" activeClassName="is-current" className="nav-item">
            <FontAwesomeIcon icon={faUserCircle} />
        </NavLink>
    </nav>
);

export default Navigation;
