import React from 'react';

export const Task = ({ taskName, handleCheckTask, completed, handleEditTask }) => {
    return (
        <li className={`task ${completed ? 'is-checked' : ''}`} id="task-1">
            <span className="checkbox" onClick={handleCheckTask}></span>
            <span className="task-name">{taskName}</span>
            <span className="edit-button" onClick={handleEditTask}><i className="fas fa-pencil-alt"></i></span>
        </li>
    )
}

