import React from 'react';
import { Task } from '../task/task.component';
import { TaskForm } from '../task-form/task-form.component';

class TaskList extends React.Component {
  constructor() {
    super();

    this.state = {
      tasks: []
    };
  }

  handleCheckTask = (e) => {
    e.target.parentElement.classList.toggle('is-checked');
    console.log(e.target.parentElement);
  }

  handleAddTask = (e) => {
    let userInput = e.target.children[0];

    let newTask = {
      userId: 999,
      id: 999,
      title: userInput.value,
      completed: false
    }
    this.setState({ tasks: [...this.state.tasks, newTask] })
    userInput.value = '';


    this.storeTask(newTask);
    e.preventDefault();
  }

  handleEditTask = (e) => {
    console.log(e.target.parentElement.parentElement);
    // let task = e.target.parentElement.parentElement;
    // let taskNameElement = e.target.parentElement.previousElementSibling;
    // // taskNameElement.style.display = 'none';
    // const input = `<input class='task-input' id='modify-input' onBlur=${console.log('it works')} value=${taskNameElement.textContent}/>`
    // // input.className = 'task-input';
    // // input.id = 'modify-input';
    // // input.value = taskNameElement.textContent;
    // taskNameElement = input;
    // document.querySelector('#modify-input').focus();
  }

  componentDidMount() {
    this.setState({ tasks: [...this.state.tasks, this.getTasksFromStorage()] });
    // this.tasks = this.getTasksFromStorage();
    // fetch('https://jsonplaceholder.typicode.com/todos')
    //   .then(response => response.json())
    //   .then(tasks => this.setState({ tasks: tasks }))
    //   .catch(err => console.log(err));
  }

  storeTask = (task) => {
    let tasks;
    localStorage.getItem('tasks');

    // Check if any tasks in LS
    if (localStorage.getItem('tasks') === null) {
      tasks = [];
      // Push new tasks
      tasks.push(task);

      // Set LS
      localStorage.setItem('tasks', JSON.stringify(tasks));
    } else {
      tasks = JSON.parse(localStorage.getItem('tasks'));

      // Push new tasks
      tasks.push(task);

      // Re set LS
      localStorage.setItem('tasks', JSON.stringify(tasks));
    }
  }
  getTasksFromStorage = () => {
    let tasks;
    if (localStorage.getItem('tasks') === null) {
      tasks = [];
    } else {
      tasks = JSON.parse(localStorage.getItem('tasks'));
    }
    return tasks;
  }


  render() {
    const { tasks } = this.state;

    return (
      <div>
        <ul id="task-list">
          {tasks.map(task => <Task taskName={task.title} handleCheckTask={this.handleCheckTask} completed={task.completed} handleEditTask={this.handleEditTask} key={task.id} />)}
        </ul>
        <TaskForm handleAddTask={this.handleAddTask} />
      </div>
    )
  }

}

